import React from 'react';
//import logo from './logo.svg';
import './App.css';

class App extends React.Component {
  state = {dolar: 0, euro:0, btc:0}
  readQuotation = () => {
    var proxyUrl = 'https://cors-anywhere.herokuapp.com/',
    targetUrl = "https://economia.awesomeapi.com.br/all/USD-BRL,EUR-BRL,BTC-BRL"
   //targetUrl = "https://swapi.co/api/people/1";
    fetch(targetUrl)
      .then(res => res.json())
      .then(data => {
        console.log(data);
        data.USD.quot = parseFloat(data.USD.ask.replace(",",".")).toLocaleString('pt-BR', {
          style: 'currency',
          currency: 'BRL',
          maximumFractionDigits: 4
        });
        if(data.USD.pctChange.replace(",",".")*1>0) data.USD.color = "green";
        else if (data.USD.pctChange.replace(",",".")*1<0) data.USD.color = "red";
        data.USD.pctChange = parseFloat(data.USD.pctChange.replace(",",".")).toLocaleString('pt-BR',{
          style: 'percent',
          maximumFractionDigits: 2
        });
        if(data.EUR.pctChange.replace(",",".")*1>0) data.EUR.color = "green";
        else if (data.EUR.pctChange.replace(",",".")*1<0) data.EUR.color = "red";
        data.EUR.pctChange = parseFloat(data.EUR.pctChange.replace(",",".")).toLocaleString('pt-BR',{
          style: 'percent',
          maximumFractionDigits: 2
        });
        if(data.BTC.pctChange.replace(",",".")*1>0) data.BTC.color = "green";
        else if (data.BTC.pctChange.replace(",",".")*1<0) data.BTC.color = "red";
        data.BTC.pctChange = parseFloat(data.BTC.pctChange.replace(",",".")).toLocaleString('pt-BR',{
          style: 'percent',
          maximumFractionDigits: 2
        });
        
        
        this.setState({dolar: data.USD, euro: data.EUR, btc: data.BTC});
      });
  }

  
  
  render(){
  if(this.state.dolar===0) this.readQuotation();
  var timer;
  clearTimeout(timer);
  timer = setTimeout(this.readQuotation,120000);

  
  return (
    <div className="App">
      <div className="bar">
        <img src="./logo.png" className="logoImg" alt="logo"/>
    
      
  <div className="footer">{/* 
    <marquee id="quotation">
  <p>Dolar: {this.state.dolar}  Euro: {this.state.euro} BTC: {this.state.btc} COPPER: 123123 ALUMINUM: 1212412 STEEL: 12312312312</p></marquee><a href="https://iexcloud.io">Data provided by IEX Cloud</a>*/}
  <p className="marquee">
  <span><randommarker className={this.state.dolar.color}><b>Dollar:</b> {this.state.dolar.quot} {this.state.dolar.pctChange}      </randommarker><randommarker className={this.state.euro.color}><b>Euro:</b> R${this.state.euro.ask} {this.state.euro.pctChange}     </randommarker><randommarker className={this.state.btc.color}><b>BTC:</b> R${this.state.btc.ask} {this.state.euro.pctChange}     </randommarker>COPPER: 123123    ALUMINUM: 1212412    STEEL: 12312312312&nbsp;</span>
  </p>
  <p className="marquee marquee2">
  <span><randommarker className={this.state.dolar.color}><b>Dollar:</b> R${this.state.dolar.ask} {this.state.dolar.pctChange}      </randommarker><randommarker className={this.state.euro.color}><b>Euro:</b> R${this.state.euro.ask} {this.state.euro.pctChange}     </randommarker><randommarker className={this.state.btc.color}><b>BTC:</b> R${this.state.btc.ask} {this.state.euro.pctChange}     </randommarker>COPPER: 123123    ALUMINUM: 1212412    STEEL: 12312312312&nbsp;</span>
  </p>
  
  </div>
  </div>
  </div>
  );
}
}
export default App;
